@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Roboto-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  src: url("../../src/fonts/Roboto-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../../src/fonts/Roboto-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 900;
  src: url("../../src/fonts/Roboto-Black.ttf") format("truetype");
}
[class$=__container] {
  width: min(95%, 120rem);
  margin: 0 auto;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  min-width: 32rem;
  font: 400 1.4rem/1.5 "Roboto", sans-serif;
}
@media (min-width: 768px) {
  body {
    font: 400 1.6rem/1.5 "Roboto", sans-serif;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1 {
  font: 900 4rem/1.2 "Roboto", sans-serif;
}

h2 {
  font: 900 3.6rem/1.2 "Roboto", sans-serif;
}

h3 {
  font: 900 3.2rem/1.2 "Roboto", sans-serif;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
}

input,
textarea {
  outline: none;
}

.header {
  padding-block: 2rem;
  margin-bottom: 4.8rem;
}
.header__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3.2rem;
}
@media (min-width: 768px) {
  .header__content {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 15rem;
  height: auto;
}
.header__menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .header__menu {
    flex-direction: row;
  }
}
.header__link {
  font-weight: 900;
  color: #000;
}
.header__link--registrar {
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  border: none;
  display: block;
  text-align: center;
  color: #FFFFFF;
  background-color: #e8505b;
  transition: background-color 0.3s ease;
}
.header__link--registrar:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .header__link--registrar {
    padding: 1.2rem;
  }
}

.formulario {
  margin-bottom: 5rem;
}
.formulario__grid {
  display: grid;
  gap: 2.4rem;
}
@media (min-width: 768px) {
  .formulario__grid {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    justify-items: center;
  }
}
@media (min-width: 1024px) {
  .formulario__grid {
    gap: 4.8rem;
  }
}
.formulario__content {
  padding: 4.8rem 2.4rem;
  background-color: #e8505b;
}
@media (min-width: 768px) {
  .formulario__content {
    height: 100%;
    padding: 3.2rem;
  }
}
@media (min-width: 1024px) {
  .formulario__content {
    padding: 4.8rem;
  }
}
@media (min-width: 1200px) {
  .formulario__content {
    padding: 10rem 4rem;
  }
}
.formulario__titulo {
  margin-bottom: 2.4rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .formulario__titulo {
    text-align: center;
    margin-bottom: 4.8rem;
  }
}
@media (min-width: 1024px) {
  .formulario__titulo {
    font-size: 4rem;
  }
}
@media (min-width: 1200px) {
  .formulario__titulo {
    font-size: 4.8rem;
  }
}
.formulario__campo {
  margin-bottom: 1.6rem;
  display: flex;
  flex-direction: column;
}
.formulario__label {
  text-transform: uppercase;
  color: #FFFFFF;
}
.formulario__input {
  padding: 0.4rem;
  border: none;
}
.formulario__buttom {
  width: 100%;
  padding: 0.8rem;
  border: none;
  text-align: center;
  text-transform: uppercase;
  font-weight: 900;
  color: #FFFFFF;
  background-color: #455a64;
  cursor: pointer;
}
.formulario__img {
  width: 100%;
  height: auto;
}

.pasos {
  overflow: hidden;
}
.pasos__ola {
  width: 100%;
  height: auto;
}
.pasos__bg {
  padding-block: 2.4rem;
  background-color: #e8505b;
}
.pasos__titulo, .pasos__subtitulo {
  text-align: center;
  color: #FFFFFF;
}
.pasos__grid {
  margin-top: 2.4rem;
  display: grid;
  justify-items: center;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .pasos__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.pasos__img {
  width: 30rem;
  height: auto;
}
@media (min-width: 768px) {
  .pasos__img {
    width: 100%;
  }
}
@media (min-width: 1024px) {
  .pasos__img:hover {
    transition: transform 0.3s ease-in-out;
    transform: rotate(-10deg) scale(1.1);
  }
}

.testimoniales {
  padding-block: 2rem;
}
@media (min-width: 768px) {
  .testimoniales {
    padding-block: 5rem;
  }
}
.testimoniales__titulo {
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .testimoniales__titulo {
    margin-bottom: 5rem;
  }
}
.testimoniales__grid {
  display: grid;
  gap: 2.4rem;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    grid-template-columns: repeat(9, 1fr);
    grid-auto-rows: 30rem;
    gap: 1.6rem;
  }
}
@media (min-width: 1024px) {
  .testimoniales__grid {
    grid-auto-rows: 25rem;
  }
}
.testimoniales__testimonial {
  height: 100%;
  padding: 1.6rem;
  border-radius: 0.8rem;
  background-color: #455a64;
}
@media (min-width: 768px) {
  .testimoniales__testimonial:nth-child(1) {
    grid-column: 1/6;
  }
  .testimoniales__testimonial:nth-child(2) {
    grid-column: 6/8;
  }
  .testimoniales__testimonial:nth-child(3) {
    grid-row: 1/3;
    grid-column: 8/10;
  }
  .testimoniales__testimonial:nth-child(4) {
    grid-column: 1/5;
  }
  .testimoniales__testimonial:nth-child(5) {
    grid-column: 5/8;
  }
}
.testimoniales__header {
  margin-bottom: 1.6rem;
  display: flex;
  align-items: center;
  gap: 1.6rem;
}
.testimoniales__img {
  width: 8rem;
  height: auto;
  border-radius: 50%;
}
.testimoniales__nombre {
  font-family: 700;
  color: #FFFFFF;
}
.testimoniales__estrella {
  width: 10rem;
  height: 1.6rem;
}
.testimoniales__texto {
  color: #FFFFFF;
}

.main {
  padding-block: 2.4rem;
  background-color: #eceeef;
}
@media (min-width: 768px) {
  .main {
    padding-block: 5rem;
  }
}
.main__titulo {
  text-align: center;
  margin-bottom: 2rem;
  color: #455a64;
}
@media (min-width: 768px) {
  .main__titulo {
    margin-bottom: 5rem;
  }
}
.main__grid {
  display: grid;
  gap: 1.6rem;
  grid-template-columns: minmax(30rem, 40rem);
  justify-content: center;
}
@media (min-width: 768px) {
  .main__grid {
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    gap: 2.4rem;
  }
}
@media (min-width: 1024px) {
  .main__grid {
    gap: 3.2rem;
  }
}
.main__restaurante {
  padding: 1.6rem;
  border-radius: 0.8rem;
  display: grid;
  gap: 2.4rem;
  justify-items: center;
  background-color: #FFFFFF;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .main__restaurante {
    padding: 2.4rem;
    grid-template-columns: 10rem 1fr;
    gap: 2.4rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .main__restaurante {
    padding: 3.2rem;
  }
}
.main__img {
  width: 20rem;
  height: auto;
}
@media (min-width: 768px) {
  .main__img {
    width: 10rem;
  }
}
.main__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
}
.main__subtitulo {
  font-size: 2.4rem;
  font-weight: 700;
  color: #e8505b;
}

.repartidor {
  padding-block: 2.4rem;
}
@media (min-width: 768px) {
  .repartidor {
    padding-block: 4.8rem;
  }
}
.repartidor__titulo {
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .repartidor__titulo {
    margin-bottom: 4.8rem;
  }
}
.repartidor__grid {
  display: grid;
  gap: 2.4rem;
}
@media (min-width: 768px) {
  .repartidor__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .repartidor__grid {
    gap: 3.2rem;
  }
}
.repartidor__img {
  width: 100%;
}
.repartidor__texto {
  margin-bottom: 1.6rem;
  line-height: 2;
}
.repartidor__buttom {
  width: 100%;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  border: none;
  display: block;
  text-align: center;
  color: #FFFFFF;
  background-color: #e8505b;
  transition: background-color 0.3s ease;
}
.repartidor__buttom:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .repartidor__buttom {
    padding: 1.2rem;
  }
}
@media (min-width: 768px) {
  .repartidor__buttom {
    padding: 1.6rem 3.2rem;
    width: auto;
  }
}

.descargar {
  padding-block: 2.4rem;
  background-color: #eceeef;
}
@media (min-width: 768px) {
  .descargar {
    padding-block: 4.8rem;
  }
}
.descargar__grid {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  gap: 4.8rem;
}
@media (min-width: 768px) {
  .descargar__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
  }
}
.descargar__img {
  width: 30rem;
  height: auto;
}
@media (min-width: 1024px) {
  .descargar__img:hover {
    transition: transform 0.3s ease-in-out;
    transform: scale(1.1) rotate(3deg);
  }
}

.tiendas {
  text-align: center;
}
.tiendas__titulo {
  margin-bottom: 0.8rem;
  color: #455a64;
}
.tiendas__grid {
  margin-top: 1.6rem;
  display: grid;
  justify-items: center;
  gap: 0.8rem;
}
@media (min-width: 768px) {
  .tiendas__grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.6rem;
  }
}
.tiendas__img {
  width: 20rem;
}
@media (min-width: 1024px) {
  .tiendas__img:hover {
    transition: transform 0.3s ease-in-out;
    transform: scale(1.2);
  }
}

.footer {
  background-color: #455a64;
}
.footer__grid {
  padding-block: 2.4rem;
  display: grid;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .footer__grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 2.4rem;
  }
}
.footer__box {
  color: #FFFFFF;
}
.footer__titulo {
  margin-bottom: 0.8rem;
  font-size: 2.4rem;
}
.footer__nav {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}
.footer__link {
  color: #FFFFFF;
}
.footer__copy {
  padding-block: 0.8rem;
  text-align: center;
  color: #FFFFFF;
  background-color: #e8505b;
}